@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,600;0,700;1,400&display=swap);
*{padding:0;margin:0;box-sizing:border-box;font-family:'Open Sans', sans-serif;list-style:none}body{background-color:#1a1a1a}.container{width:600px;margin:auto;color:#fff}

.header{padding:30px 0px}.header h1{font-size:40px;text-transform:uppercase;color:#c8ffce}

.addTodo{padding:10px 0px;display:flex}.addTodo input{padding:10px;width:380px;flex:1 1;border-radius:5px 0px 0px 5px;border:0;background-color:#2c2c2c;color:white;font-size:18px;outline:none}.addTodo button{background-color:#2c2c2c;border:0;border-radius:0px 5px 5px 0px;color:white;padding:5px 15px;font-size:25px;outline:none;cursor:pointer}

.listTodo h3{color:#3b3b3b;margin-bottom:20px}.listTodo ul li{background-color:#2c2c2c;margin:20px 0px;padding:0px 0px 0px 20px;border-radius:5px;font-size:18px;display:flex;justify-content:space-between;align-items:center}.listTodo ul li button{height:60px;font-size:20px;outline:none;padding:10px;border-radius:0px 5px 5px 0;background-color:#2c2c2c;cursor:pointer;border:0;color:white}

.doneTodo .toogleCollapse{display:flex;justify-content:space-between;align-items:center;background-color:#c8ffce;padding:10px;color:#1a1a1a;border-radius:5px}.doneTodo h3{color:#3b3b3b;margin-bottom:20px}.doneTodo ul{margin-top:10px}.doneTodo ul li{background-color:#0ec45a;margin:0px 0px 10px 0px;padding:0px 0px 0px 20px;border-radius:5px;font-size:20px;display:flex;justify-content:space-between;align-items:center}.doneTodo ul li button{outline:none;height:60px;padding:10px;border-radius:0px 5px 5px 0;background:#0ec45a;border:0;color:white;font-size:20px;text-align:center;cursor:pointer}.doneTodo ul li span{color:#fff;text-decoration:line-through}

